/** @jsx jsx */
import { jsx } from '@emotion/react'; // to avoid unused var
import React from 'react';
import PromptContext, {
  PromptContextValue,
  PromptShowOptions,
  PromptShowResultOK,
} from '../contexts/PromptContext';
import * as style from '../styles/prompt.style';

// TODO: IN PROGRESS https://sh.nullx.me/rilez
const Prompt: React.FC = (props) => {
  const overlayRef = React.createRef<HTMLDivElement>();
  const promptContainerAbsRef = React.createRef<HTMLDivElement>();
  const promptContainerRef = React.createRef<HTMLDivElement>();
  const promptTextContainerRef = React.createRef<HTMLDivElement>();
  const promptButtonsRef = React.createRef<HTMLDivElement>();
  const promptTitleRef = React.createRef<HTMLHeadingElement>();
  const promptDescRef = React.createRef<HTMLParagraphElement>();
  const promptInputRef = React.createRef<HTMLInputElement>();
  const promptOKRef = React.createRef<HTMLButtonElement>();
  const promptCancelRef = React.createRef<HTMLButtonElement>();

  const promptShow = async ({
    title,
    message,
    buttons,
    placeholder = '',
    defaultValue = '',
  }: PromptShowOptions) => {
    return new Promise<PromptShowResultOK>((resolve, reject) => {
      if (
        !promptCancelRef.current ||
        !promptOKRef.current ||
        !promptInputRef.current ||
        !promptTitleRef.current ||
        !promptDescRef.current ||
        !promptButtonsRef.current ||
        !promptContainerRef.current ||
        !promptContainerAbsRef.current ||
        !promptTextContainerRef.current ||
        !overlayRef.current
      )
        return reject(new Error('Prompt is not ready'));

      const dispose = (success = true) => {
        if (!overlayRef.current || !promptContainerAbsRef.current)
          throw new Error('Prompt is not ready');
        overlayRef.current.style.display = 'none';
        promptContainerAbsRef.current.style.display = 'none';

        if (success && promptInputRef.current)
          return resolve({ value: promptInputRef.current.value });
        else if (!promptInputRef.current) {
          return reject(new Error('Prompt input is not defined'));
        }

        return reject(new Error('Canceled'));
      };

      promptCancelRef.current.onclick = () => dispose(false);
      promptOKRef.current.onclick = () => dispose(true);
      promptInputRef.current.onkeyup = (e) => {
        if (e.key === 'Enter') {
          dispose();
        } else if (e.key === 'Escape') {
          dispose(false);
        }
      };

      if (buttons.includes('CANCEL')) {
        promptCancelRef.current.style.display = 'block';
      }

      if (buttons.includes('OK')) {
        promptOKRef.current.style.display = 'block';
      }

      promptTitleRef.current.textContent = title;
      promptDescRef.current.textContent = message;
      promptInputRef.current.value = defaultValue;
      promptInputRef.current.placeholder = placeholder;

      promptContainerAbsRef.current.style.display = 'block';
      overlayRef.current.style.display = 'block';
    });
  };

  const promptValue: PromptContextValue = [promptShow];

  return (
    <PromptContext.Provider value={promptValue}>
      <div ref={overlayRef} css={style.promptOverlayStyle} />
      <div ref={promptContainerAbsRef} css={style.promptContainerAbsStyle}>
        <div ref={promptContainerRef} css={style.promptContainerStyle}>
          <div
            ref={promptTextContainerRef}
            css={style.promptTextContainerStyle}
          >
            <h3 ref={promptTitleRef} css={style.promptTitleStyle}>
              Title
            </h3>
            <p ref={promptDescRef} css={style.promptDescStyle}>
              Desc
            </p>
          </div>
          <input ref={promptInputRef} css={style.promptInputStyle} />
          <div ref={promptButtonsRef} css={style.promptButtonsStyle}>
            <button ref={promptCancelRef} css={style.promptCancelStyle}>
              Cancel
            </button>
            <button ref={promptOKRef} css={style.promptSubmitStyle}>
              Ok
            </button>
          </div>
        </div>
      </div>
      {props.children}
    </PromptContext.Provider>
  );
};

export default Prompt;
export { usePrompt } from '../contexts/PromptContext';
